import React, { Component } from 'react';
import './App.css';
import request from 'superagent'
import Notifications, { notify } from 'react-notify-toast';

export default class App extends Component {
  state = {
    currentLocationWeather: null,
    value: '',
    city: []
  };

  componentDidMount() {
    navigator.geolocation.getCurrentPosition(this.getCurrentLocationWeather);
  }

  getCurrentLocationWeather = coordinates => {
    const lat = coordinates.coords.latitude;
    const lon = coordinates.coords.longitude;

    request
      .get('https://api.weatherbit.io/v2.0/current')
      .query({ lat })
      .query({ lon })
      .query({ 'key': 'd5c3391c63af47658c84946c5fe654e5' })
      .then(res => {
        if (res.body) {
          this.setState({
            currentLocationWeather: res.body.data[0]
          });
        } else {
          notify.show('No results for your location');
        }
      })
      .catch(err => {
        notify.show(err.message);
      });
  }

  handleChange = (event) => {
    this.setState({ value: event.target.value });
  }

  handleSubmit = (event) => {
    request
      .get('https://api.weatherbit.io/v2.0/current')
      .query({ 'city': this.state.value })
      .query({ 'key': 'd5c3391c63af47658c84946c5fe654e5' })
      .then(res => {
        if (res.body) {
          this.setState(prevState => ({
            city: [...prevState.city, res.body.data[0]]
          }));
        } else {
          notify.show('No results for choosen city');
        }
      })
      .catch(err => {
        notify.show(err.message);
      });
    event.preventDefault();
  }

  render() {

    const weather = this.state.city.map((city, index) => {
      return (
        <div key={index}>
          <h5>{city.city_name}</h5>
          <h6>{city.weather.description}</h6>
          <img height="50" width="50" alt={city.weather.icon} src={`https://www.weatherbit.io/static/img/icons/${city.weather.icon}.png`}></img>
        </div>
      )
    })

    return (
      <div className="App">
        <Notifications />
        <header className="App-header">
          <form onSubmit={this.handleSubmit}>
            <input type="text" value={this.state.value} onChange={this.handleChange} />
            <input type="submit" value="Submit" className="btn btn-dark" />
          </form>
          {
            this.state.currentLocationWeather
              ?
              <div>
                <h4>Weather on your location</h4>
                <h5>{this.state.currentLocationWeather.city_name}</h5>
                <h6>{this.state.currentLocationWeather.weather.description}</h6>
                <h6>{this.state.currentLocationWeather.temp}&deg;</h6>
                <img height="50" width="50" alt={this.state.currentLocationWeather.weather.icon} src={`https://www.weatherbit.io/static/img/icons/${this.state.currentLocationWeather.weather.icon}.png`}></img>
              </div>
              :
              null
          }
          {
            this.state.city.length
              ?
              weather
              :
              null
          }
        </header>
      </div>
    );
  }
}
